import axios from "axios";

const api = axios.create({
  baseURL: "https://api.themoviedb.org/3/",
  params: {
    api_key: "cc6577a51fc110a4005fbdfbdaf9a394",
    language: "en-US",
  },
});

export const tvApi = {
  topRated: () => api.get("tv/top_rated"),
  popular: () => api.get("tv/popular"),
  airingToday: () => api.get("tv/airing_today"),
  showDetail: (id) =>
    api.get(`tv/${id}`, {
      params: {
        append_to_response: "videos,images",
      },
    }),
  search: (term) =>
    api.get("search/tv", { params: { query: encodeURIComponent(term) } }),
  externalIDs: (id) => api.get(`tv/${id}/external_ids`),
};

export const moviesApi = {
  nowPlaying: () => api.get("movie/now_playing"),
  upcoming: () => api.get("movie/upcoming"),
  popular: () => api.get("movie/popular"),
  movieDetail: (id) =>
    api.get(`movie/${id}`, {
      params: {
        append_to_response: "videos,images",
      },
    }),
  search: (term) =>
    api.get("search/movie", { params: { query: encodeURIComponent(term) } }),
  externalIDs: (id) => api.get(`movie/${id}/external_ids`),
};

export const collectionApi = (id) => api.get(`collection/${id}`);
